.span {
    height:40px;
    padding: 5px;
    font-family: Arial, sans-serif;
}
.span:hover {
    font-weight: bold;
    cursor: pointer;
    color: #0aca0a;
}
.superSpan {

    justify-content: center;
    display: flex;

}

.superSpan:hover::after{
    content: attr(data-title);
    position: absolute;
    bottom:550px;
    z-index: 1;
    background: lightyellow;
    font-family: Arial, sans-serif;
    font-size: 10px;
    padding: 5px 10px;
    border: 1px solid #333;

}
